  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  font: 18px "Helvetica Neue", Helvetica, Arial;
  background-color: #f5f5f5;
  color: #4d4d4d;
  font-weight: 300;
}

.header-image {
  width: 100%;
  object-fit: cover;
  height: 200px;
}

h1 {
  font-weight: 200;
  text-align: center;
  font-size: 80px;
  margin: 50px 0;
}

.todolist-wrapper {
  width: 80%;
  margin: 0 auto;
  background-color: #fff;
  box-shadow: 3px 4px 5px 0px rgba(0, 0, 0, 0.75);
}
.todolist-wrapper .new-todo {
  margin: 0;
  width: 100%;
  font-family: "Helvetica Neue";
  font-size: 24px;
  font-weight: 300;
  padding: 16px;
  border: none;
  border-bottom: 1px solid #ededed;
  outline: none;
}
.todolist-wrapper .todo-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.todolist-wrapper .todo-list li {
  display: flex;
  align-items: center;
  font-size: 20px;
  border-bottom: 1px solid #ededed;
}
.todolist-wrapper .todo-list li .custom-checkbox {
  position: relative;
  width: 30px;
  height: 30px;
  margin-left: 15px;
  border: 1px solid #777;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.todolist-wrapper .todo-list li .custom-checkbox img {
  display: none;
}
.todolist-wrapper .todo-list li .custom-checkbox .real-checkbox {
  width: 30px;
  height: 30px;
  cursor: pointer;
  opacity: 0;
  position: absolute;
  top: -3px;
  left: -5px;
}
.todolist-wrapper .todo-list li.completed .custom-checkbox img {
  display: block;
}
.todolist-wrapper .todo-list li label {
  flex: 1;
  word-break: break-all;
  white-space: pre-wrap;
  margin: 0;
  padding: 15px;
}
.todolist-wrapper .todo-list li .delete {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  cursor: pointer;
  visibility: hidden;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center no-repeat;
  background-size: 90%;
}
.todolist-wrapper .todo-list li:hover .delete {
  visibility: visible;
}

.motivational-pictures-container {
  display: flex;
  justify-content: center;
}
.motivational-pictures-container .motivational-pictures {
  display: flex;
  gap: 50px;
  margin: 200px auto;
}
.motivational-pictures-container .motivational-pictures img {
  width: 200px;
  height: 200px;
  border-radius: 8px;
